//  在此封装一个axios 的post 请求
//  给axios post请求一个默认参数{}
//  axios post 第三个参数用来配置其他信息
//  then 把reponse的data数据传出去，去掉冗余信息

import axios from 'axios'

const instance = axios.create({
  baseURL: 'https://www.fastmock.site/mock/2484e421d80e10d5c7b0b33b5e465c4b/demo',
  timeout: 5000 //  超时时间
})

export const post = (url, data = {}) => {
  return new Promise((resolve, reject) => {
    instance.post(url, data, {
      headers: {
        'Content-Type': 'application/json'
      }
    }).then(response => { resolve(response.data) }, err => { reject(err) })
  })
}

export const get = (url, params = {}) => {
  return new Promise((resolve, reject) => { instance.get(url, params).then(response => resolve(response.data)).catch(err => reject(err)) })
}
