<template>
    <div class="toast">{{msg}}</div>
</template>

<script>
import { reactive } from 'vue'

export default {
  name: 'Toast',
  props: ['msg']
}
export const useToastEffect = () => {
  //  toastDate
  const toastDate = reactive({ toastDisplay: false, toastmsg: '' })
  // 封装showToast
  const showToast = (text) => {
    toastDate.toastDisplay = true
    toastDate.toastmsg = text
    setTimeout(() => {
      toastDate.toastDisplay = false
      toastDate.toastmsg = ''
    }, 1000)
  }
  return { toastDate, showToast }
}
</script>

<style lang="scss" scoped>
.toast {
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-3.3rem,-1rem);
    font-size: .16rem;
    padding: .1rem;
    background: rgba(0, 0, 0, 0.4);
    color: #fff;
    border-radius: .1rem;
}
</style>
