<template>
<div class="todoitemwrapper">
    <div class="todoitemwrapper__ico iconfont" @click="() => {changeItemClick(data)}">
        {{data.completed? '&#xe62d;': '&#xe70f;'}}
    </div>
    <div :class="{todoitemwrapper__text:true, todoitemwrapper__textcompleted: data.completed}">
        {{data.title}}
    </div>
    <div class="todoitemwrapper__container">
        <div
        :class="{ todoitemwrapper__container__flag:true, iconfont:true, todoitemwrapper__container__flaged:data.flag  }"
        @click="() => {changeFlagClick(data)}">
            &#xe70a;
        </div>
        <div class="todoitemwrapper__container__delete iconfont" @click="() => {deleteItemClick(data)}">&#xe606;</div>
    </div>
</div>
</template>

<script>
import { useStore } from 'vuex'
export default {
  props: ['data'],
  name: 'Todoitem',
  setup () {
    const store = useStore()
    const changeItemClick = (data) => {
      store.commit('changeCompletedState', data)
    }
    const changeFlagClick = (data) => {
      store.commit('changeFlagState', data)
    }
    const deleteItemClick = (data) => {
      store.commit('deleteLitItem', data)
    }
    return { changeItemClick, changeFlagClick, deleteItemClick }
  }
}
</script>

<style lang="scss" scoped>

.todoitemwrapper {
    display: flex;
    justify-items: center;
    height: 0.5rem;
    padding: .1rem;
    // width: 100%;
    background-color: #c7ffd8;
    border-bottom: .02rem solid #161d6f;
    &:hover {
        transform: translate(.05rem, .05rem);
        border: 2px solid #161d6f;
        transition: .8s all ease;
    }
    &:active {
        background-color: cadetblue;
    }
    &__ico {
        text-align: center;
        width: .4rem;
        height: 0.5rem;
        margin-right: .2rem;
        line-height: 0.5rem;
        font-size: .25rem;
        cursor: pointer;
    }
    &__text {
        flex: 1;
        height: 0.5rem;
        line-height: 0.5rem;
        font-size: .3rem;
        white-space:nowrap;
        overflow:hidden;
        text-overflow:ellipse;
        &completed{
            text-decoration: line-through;
            color:rgb(99, 99, 99);
        }
    }
    &__container {
        height: 0.5rem;
        line-height: 0.5rem;
        font-size: .25rem;
        text-align: center;
        width: .8rem;
        display: flex;
        &__flag {
            flex: 1;
            cursor: pointer;
            border-right: .02rem solid #98ded9;
            &ed {
                color:red;
            }
        }
        &__delete {
            flex: 1;
            cursor: pointer;
        }
    }
}
</style>
