<template>
    <div class="wrapper">
      <div class="wrapper__item">Title</div>
      <div class="wrapper__item">ToDoList
        <button class="btn" @click = "handleToListPageClick" >进入todolist页面</button>
      </div>
      <div class="wrapper__item">
        <button class="btn" @click = "handleLogPageClick" >进入Log页面</button>
      </div>
    </div>
</template>

<script>
import { useRouter } from 'vue-router'

const usetodolistbtnEffect = () => {
  const router = useRouter()
  const handleToListPageClick = () => {
    router.push({ name: 'Todolist' })
  }
  return { handleToListPageClick }
}
const uselogbtnEffect = () => {
  const router = useRouter()
  const handleLogPageClick = () => {
    router.push({ name: 'Log' })
  }
  return { handleLogPageClick }
}

export default {
  name: 'Home',
  setup () {
    const { handleToListPageClick } = usetodolistbtnEffect()
    const { handleLogPageClick } = uselogbtnEffect()
    return { handleToListPageClick, handleLogPageClick }
  }
}
</script>

<style lang="scss" scoped>
// 本页面的样式和结构需要重构
.wrapper {
  display: flex;
  flex-direction: column;
  width: 500px;
  height: 500px;
  color: rgba(51, 39, 161, 0.726);
  background-color: cadetblue;
  &__item {
    flex: 1;
    // box-sizing: border-box;
    border: 1px  solid red;
  }
}
.btn {
  width: 100px;
  height: 100px;
  background-color: green;
}
</style>
