import { createStore } from 'vuex'
// import { get } from '../utils/request'
export default createStore({
  state: {
    todolist: [
      {
        _id: '1',
        title: '我我我',
        content: 'asdasdadasdda',
        date: '',
        markcolor: 'red',
        flag: false,
        completed: false
      },
      {
        _id: '2',
        title: '你你牛',
        content: 'xasdadadasdasdasdasdasdad',
        date: '',
        markcolor: 'red',
        flag: false,
        completed: true
      },
      {
        _id: '3',
        title: '他他他',
        content: 'xxxxxxxxxxxxxxxxxxxxxxxxxxxx',
        date: '',
        markcolor: 'red',
        flag: false,
        completed: false
      },
      {
        _id: '4',
        title: 'tatata',
        content: 'xxxxxxxxxxxxxxxxxxxxxxxxxxxxxx',
        date: '',
        markcolor: 'red',
        flag: false,
        completed: false
      }
    ]
  },
  mutations: {
    changeCompletedState (state, item) {
      this.state.todolist.map(o => {
        if (o._id === item._id) {
          o.completed = !o.completed
        }
      })
    },
    changeFlagState (state, item) {
      this.state.todolist.map(o => {
        if (o._id === item._id) {
          o.flag = !o.flag
        }
      })
    },
    addListItem (state, data) {
      this.state.todolist.push({
        _id: Date.now().toString(),
        title: data,
        content: '',
        date: '',
        markcolor: 'red',
        flag: false,
        completed: false
      })
    },
    deleteLitItem (state, data) {
      this.state.todolist.map(o => {
        if (o._id === data._id) {
          this.state.todolist.splice(this.state.todolist.indexOf(o), 1)
        }
      })
    }
  },
  actions: {
  //   getdata () {
  //     const getdatafromserver = async () => {
  //       const result = get('/api/getlist')
  //       console.log(result)
  //     }
  //     return { getdatafromserver }
  //   }
  },
  modules: {
  }
})
