<template>
  <div class="wrapper">
    <div class="wrapper__main">
      <div class="wrapper__main__username">
        <div class="wrapper__main__username__ico iconfont">&#xe658;</div>
        <input
          class="wrapper__main__username__input"
          placeholder="请输入用户名"
          v-model="data.username"
        />
      </div>
      <div class="wrapper__main__password">
        <div class="wrapper__main__password__ico iconfont">&#xe62b;</div>
        <input
          class="wrapper__main__password__input"
          type="password"
          placeholder="请输入密码"
          v-model="data.password"
        />
      </div>

      <div class="wrapper__main__loginbtn" @click="handleLogin">Login</div>
      <div class="wrapper__main__toregister" @click="handleRegisterClick">若无账号请点此注册</div>
    </div>
    <div class="wrapper__pic"></div>
  </div>
  <Toast v-if="toastDate.toastDisplay" :msg="toastDate.toastmsg" />
</template>

<script>
import { useRouter } from 'vue-router'
import { reactive } from 'vue'
import { post } from '../../utils/request'
import Toast, { useToastEffect } from '../../components/Toast.vue'
// 引入自己封装的axios 的 post请求函数
// 抽离登录逻辑
const useLoginEffect = (showToast) => {
  const router = useRouter()
  const data = reactive({ username: '', password: '' })
  const handleLogin = async () => {
    const { username, password } = data
    // 输入校验，确保都输入了
    if (username.trim() === '' || password.trim() === '') {
      showToast('请输入用户名和密码')
      return
    }
    //  用try catch语句捕获错误，防止因为请求出错导致的语句不继续执行
    try {
      const result = await post('/api/login', {
        username: data.username,
        password: data.password
      })
      //  用.?防止报错，因为data 可能不存在
      if (result?.errno === 0) {
        localStorage.isLogin = true
        router.push({ name: 'Home' })
      } else {
        // 登录失败->弹出toast->2s后消失
        showToast('登录失败')
      }
    } catch (e) {
      showToast('请求失败')
    }
  }
  return { data, handleLogin }
}
// 抽离跳转到注册页面逻辑
const useRegisterEffect = () => {
  const router = useRouter()
  const handleRegisterClick = () => {
    router.push({ name: 'Register' })
  }
  return { handleRegisterClick }
}
export default {
  components: { Toast: Toast },
  name: 'Login',
  setup () {
    const { toastDate, showToast } = useToastEffect()
    const { data, handleLogin } = useLoginEffect(showToast)
    const { handleRegisterClick } = useRegisterEffect()
    return { toastDate, showToast, data, handleLogin, handleRegisterClick }
  }
}
</script>

<style lang="scss" scoped>
.wrapper {
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left:-4rem;
  margin-top:-2.5rem;
  width: 8rem;
  height: 5rem;
  box-shadow: 10px 5px 10px cadetblue;
  &__main {
    position: relative;
    flex: 250;
    background-color: #ccc;
    &__username {
      position: absolute;
      left: .2rem;
      top:1.3rem;
      display: flex;
      box-sizing: border-box;
      width: 2rem;
      height: .4rem;
      background-color: #fff;
      border-bottom: .02rem solid green;
      &__ico {
        width: .25rem;
        height: .40rem;
        line-height: .4rem;
        font-size: .2rem;
      }
      &__input {
        flex: 1;
        box-sizing: border-box;
        outline: none;
        background: none;
        border:none;
        padding-right: 10px;
      }
    }
    &__password {
      position: absolute;
      left: .2rem;
      top:1.8rem;
      display: flex;
      box-sizing: border-box;
      width: 2rem;
      height: .4rem;
      background-color: #fff;
      border-bottom: .02rem solid green;
      &__ico {
        width: .25rem;
        height: .40rem;
        line-height: .4rem;
        font-size: .2rem;
      }
      &__input {
        flex: 1;
        box-sizing: border-box;
        outline: none;
        background: none;
        border:none;
        padding-right: 10px;
      }
    }

    &__loginbtn {
      position: absolute;
      right: 0.3rem;
      bottom: 1.9rem;
      padding: 0 .1rem;
      height: 0.32rem;
      line-height: .32rem;
      border-radius: .2rem;
      text-align: center;
      background-color: cadetblue;
      color: #fff;
      &:hover {
        cursor: pointer;
        box-shadow: .02rem .02rem .02rem black;
     }
    }
    &__toregister {
      position: absolute;
      bottom: .1rem;
      right: .1rem;
      font-size: .12rem;
      &:hover {
        cursor: pointer;
      }
    }
  }
  &__pic {
    flex: 550;
    background-color: #fff;
  }
}
</style>
