<template>
  <div class="wrapper">
    <div class="wrapper__main">
      <div class="wrapper__main__username">
        <div class="wrapper__main__username__ico iconfont">&#xe658;</div>
        <input
          class="wrapper__main__username__input"
          v-model="username"
          placeholder="请输入用户名"
        />
      </div>
      <div class="wrapper__main__password">
        <div class="wrapper__main__password__ico iconfont">&#xe62b;</div>
        <input
          class="wrapper__main__password__input"
          type="password"
          v-model="password"
          placeholder="请输入密码"
        />
      </div>
      <div class="wrapper__main__password-ensure">
        <div class="wrapper__main__password__ico iconfont">&#xe62b;</div>
        <input
          class="wrapper__main__password__input"
          type="password" v-model="ensurepassword"
          placeholder="请确认密码"
        />
      </div>
      <div class="wrapper__main__checclause">
        <input class="wrapper__main__checclause__checkbox" type="checkbox" />
        <p class="wrapper__main__checclause__text">同意条款</p>
      </div>
      <div class="wrapper__main__registerbtn" @click="handleRegisterClick">Register</div>
      <div class="wrapper__main__tologin" @click="handleLoginClick">若有账号请点此登录</div>
    </div>
    <div class="wrapper__pic"></div>
  </div>
  <Toast v-if="toastDate.toastDisplay" :msg="toastDate.toastmsg" />
</template>

<script>
import { useRouter } from 'vue-router'
import { reactive, toRefs } from 'vue'
import { post } from '../../utils/request'
import Toast, { useToastEffect } from '../../components/Toast.vue'
//  抽离注册相关逻辑
const useRegisterEffect = (showToast) => {
  const router = useRouter()
  const data = reactive({ username: '', password: '', ensurepassword: '' })
  const handleRegisterClick = async () => {
  //  用try catch语句捕获错误，防止因为请求出错导致的语句不继续执行
    try {
      const result = await post('/api/user/register1', {
        username: username,
        password: password,
        ensurepassword: ensurepassword
      })
      //  用.?防止报错，因为errno 可能不存在
      if (result?.errno === 0) {
        router.push({ name: 'Login' })
      } else {
        // 注册失败->弹出toast->2s后消失
        showToast('注册失败')
      }
    } catch (e) {
      showToast('请求失败')
    }
  }
  const { username, password, ensurepassword } = toRefs(data)
  return { username, password, ensurepassword, handleRegisterClick }
}
// 抽离跳转到登录页面逻辑
const useLoginEffect = () => {
  const router = useRouter()
  const handleLoginClick = () => {
    router.push({ name: 'Login' })
  }
  return { handleLoginClick }
}

export default {
  components: { Toast },
  name: 'Register',
  setup () {
    const { toastDate, showToast } = useToastEffect()
    const { username, password, ensurepassword, handleRegisterClick } = useRegisterEffect(showToast)
    const { handleLoginClick } = useLoginEffect()
    return { toastDate, showToast, handleLoginClick, username, password, ensurepassword, handleRegisterClick }
  }
}
</script>

