<template>
   <div class="wrapper" >
       <div class="header">
           <div class="header__title">
               <div class="header__title__logo">
                   <img class="header__title__logo__img" src="../../assets/todolistlogo.png" alt="logo">
               </div>
           </div>
           <div class="header__search">
               <div class="header__search__ico iconfont">&#xe601;</div>
               <input class="header__search__input" type="text" placeholder="这里是placeholder"/>
           </div>
           <div class="header__menu">
               <div class="header__menu__set iconfont">&#xe63a;</div>
               <div class="header__menu__help iconfont">&#xe6af;</div>
               <div class="header__menu__user"><img src="../../assets/userheadimg.png" alt="userheadimg"></div>
           </div>
       </div>
       <div class="content">
           <div class="sliderbar">
               <div class="sliderbar__wrapper">
                   <div class="sliderbar__theme iconfont">&#xe6b3;</div>
                   <div class="sliderbar__mark iconfont">&#xe70a;</div>
                   <div class="sliderbar__calendar iconfont">&#xe622;</div>
               </div>
               <div class="sliderbar__home sliderbar__home--active iconfont">&#xe602;</div>
           </div>
           <div class="listarea">
               <div class="listarea__add iconfont">
                <!-- 在此应该回车也能触发事件 -->
                <div class="listarea__add__layout">
                    <input class="listarea__add__layout__input" type="text" placeholder="toDoItem" v-model="InputValue" @keyup.enter="()=>{ addListItem(InputValue) }">
                    <div class="listarea__add__layout__click" @click="()=>{ addListItem(InputValue) }">Add</div>
                </div>
               </div>
               <div class="listarea__todo">
                   <div class="listarea__todo__title">
                       <span :class="{listarea__todo__title__ico:true, iconfont:true, listarea__todo__title__icohidden:!show.todo}" @click="changeToDoShowState">&#xe62e;</span>
                       <span class="listarea__todo__title__text">ToDo </span>
                   </div>
                   <!-- transition标签 动画效果包裹 -->
                   <transition name="todo">
                   <div class="listarea__todo__box" v-show="show.todo">
                            <template v-for = "item in todolist" :key="item._id">
                                <Todoitem :data = item v-if="!item.completed" />
                            </template>
                   </div>
                   </transition>
               </div>
               <div class="listarea__complete">
                   <div class="listarea__complete__title">
                       <span :class="{listarea__complete__title__ico:true, iconfont:true, listarea__complete__title__icohidden:!show.complete}" @click="changeCompleteShowState">&#xe62e;</span>
                       <span class="listarea__complete__title__text">Complete </span>
                   </div>
                   <!-- transition标签 动画效果包裹 -->
                   <transition name="complete">
                   <div class="listarea__complete__box" v-show="show.complete">
                       <template v-for = "item in todolist" :key ="item._id" v-show="show.complete">
                       <Todoitem :data = item v-if="item.completed" />
                       </template>
                   </div>
                   </transition>
               </div>
           </div>
       </div>
   </div>
</template>

<script>
import { reactive } from 'vue'
import { useStore } from 'vuex'
import Todoitem from './Todoitem'
export default {
  name: 'Todolist',
  components: { Todoitem },
  setup () {
    let InputValue = ''
    const show = reactive({ todo: true, complete: true })
    const store = useStore()
    const todolist = store.state.todolist
    const addListItem = (data) => {
      InputValue = ''
      store.commit('addListItem', data)
    }
    const changeToDoShowState = () => {
      show.todo = !show.todo
    }
    const changeCompleteShowState = () => {
      show.complete = !show.complete
    }
    return { todolist, InputValue, addListItem, show, changeToDoShowState, changeCompleteShowState }
  }
}
</script>

<style lang="scss" scoped>
// vue动画效果
//addtodoitem
.todoitem-enter-from {
    opacity: 0;
}
.todoitem-enter-active {
    transition: 1s opacity ease;
}
.todoitem-enter-to {
    opacity: 1;
}
//todolist
.todo-enter-from {
    opacity: 0;
}
.todo-enter-active {
    transition: 1s opacity ease;
}
.todo-enter-to {
    opacity: 1;
}
//completelist
.complete-enter-from {
    opacity: 0;
}
.complete-enter-active {
    transition: 1s opacity ease;
}
.complete-enter-to {
    opacity: 1;
}

.wrapper {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
}
//  header
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: .6rem;
    background-color: #5f9ea0;
    .iconfont {
        font-size: .32rem;
    }
    &__title {
        padding-left: .2rem;
        &__logo {
            &__img {
                height: 0.6rem;
            }
        }
    }
    &__search {

        display: flex;
        align-items: center;
        box-sizing: border-box;
        width: 3.5rem;
        height: 0.5rem;
        padding: .1rem 0 .1rem .15rem;
        background-color: #ccc;
        border-radius: .1rem;
        &__ico {
            font-size: .32rem;
            width: 0.4rem;
            height: 0.4rem;
            text-align: center;
            line-height: 0.4rem;
        }
        &__input {
            height: 100%;
            width: 100%;
            box-sizing: border-box;
            border: none;
            background: none;
            outline: none;
            padding-right: .2rem;
            font-size: .26rem;
        }
    }
    &__menu {
        margin-right: .2rem;
        display: flex;
        align-items: center;
        width: 1.8rem;
        &__set {
            text-align: center;
            flex: 1;
            height: 0.6rem;
            line-height: 0.6rem;
        }
        &__help {
            text-align: center;
            flex: 1;
            height: 0.6rem;
            line-height: 0.6rem;
        }
        &__user {
            width: 0.5rem;
            height: 0.5rem;
            box-sizing: border-box;
            border-radius: 50%;
            border: .05rem solid green;
            overflow: hidden;
            img {
                width: 100%;
                height: 100%;
            }
        }
    }
}
//  content
.content {
    flex: 1;
    display: flex;
    //去除li的样式，模块分割时需重构此处
    //sliderbar
    .sliderbar {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        width: 0.4rem;
        background-color: #ccc;
        &__wrapper {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            height: 2.4rem;
        }
        &__theme {
            flex: 1;
            font-size: .3rem;
            line-height: .8rem;
        }
        &__mark {
            flex: 1;
            font-size: .3rem;
            line-height: .8rem;
        }
        &__calendar {
            flex: 1;
            font-size: .3rem;
            line-height: .8rem;
        }
    }
    .sliderbar__home {
            font-size: .3rem;
            line-height: .8rem;
            &--active {
            color: cadetblue;
        }
    }
    //listarea
    .listarea {
        //防止过长溢出
        overflow: hidden;
        background-color: #f6f6f6;
        flex: 1;
        &__add {
            position: relative;
            height: .8rem;
            &__layout {
                position: absolute;
                display: flex;
                height: .4rem;
                width: 5rem;
                top: 50%;
                left: 50%;
                transform: translate(-50%,-50%);
                &__input {
                    flex: 1;
                    border: none;
                    outline: none;
                    background: none;
                    box-sizing: border-box;
                    border-bottom: .04rem solid cadetblue;
                    padding: 0 .1rem;
                }
                &__click {
                    width: 0.5rem;
                    margin-left: .05rem;
                    background-color: cadetblue;
                    border-radius: .1rem;
                    text-align: center;
                    height: 0.4rem;
                    line-height: .4rem;
                    font-weight: 700;
                    color: #ccc;
                    &:hover {
                        box-shadow: .03rem .03rem .03rem rgba(114, 113, 113, 0.315);
                        cursor: pointer;
                    }
                    &:active {
                        box-shadow: .03rem .03rem .03rem rgba(214, 0, 0, 0.315);
                        cursor: pointer;
                    }
                }
            }
        }
        &__todo {
            &__title {
                margin-bottom: .2rem;
                &__ico {
                    transition: 0.5s transform ease;
                    display: inline-block;
                    &hidden {
                        transform: rotate(-90deg);
                    }
                    cursor: pointer;
                    font-size: .3rem;
                }
                &__text {
                    font-size: .3rem;
                }
            }
            &__box {
                padding: 0 .2rem;
                margin-bottom: .2rem;
            }
        }
        &__complete {
            &__title {
                margin-bottom: .2rem;
                &__ico {
                    transition: 0.5s transform ease;
                    display: inline-block;
                    &hidden {
                        transform: rotate(-90deg);
                    }
                    cursor: pointer;
                    font-size: .3rem;
                }
                &__text {
                    font-size: .3rem;
                }
            }
            &__box {
                padding: 0 .2rem;
                margin-bottom: .2rem;
            }
        }
    }
}
</style>
